import { Badge } from "@material-ui/core";
import { ShoppingCartOutlined } from "@material-ui/icons";
import React from "react";
import { useHistory } from "react-router";
import styled from "styled-components";
import { useGlobalContext } from "../context";
import { mobile } from "../responsive";

const Container = styled.div`
  height: 60px;
  margin-top: -10px;
  max-width: 100vw;
  ${mobile({ height: "50px" })}
`;

const Wrapper = styled.div`
  padding: 5px 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  ${mobile({ padding: "10px 0px" })}
`;

const Center = styled.div`
  flex: 1;
  //text-align: center;
`;

const Logo = styled.h3`
  font-weight: bold;

  ${mobile({ fontSize: "24px" })}
`;
const LogoText = styled.span`
  border: 0.5px solid lightgray;
  cursor: pointer;
  padding: 5px;
  letter-spacing: 1px;
  color: purple;
`;
const Right = styled.div`
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  ${mobile({ flex: 2, justifyContent: "center" })}
`;

const MenuItem = styled.div`
  font-size: 14px;
  cursor: pointer;
  margin-left: 25px;
  ${mobile({ fontSize: "12px", marginLeft: "10px" })}
`;

const Navbar = () => {
  const history = useHistory();
  const { cart, clearCart, user, setUser, initialUser } = useGlobalContext();

  const logout = () => {
    setUser(initialUser);
    clearCart();
  };
  return (
    <Container>
      <Wrapper>
        <Center>
          <Logo>
            <LogoText onClick={() => history.push("/")}> E-GROCERY</LogoText>
          </Logo>
        </Center>
        <Right>
          {user.name && (
            <MenuItem>
              Hello!{" "}
              <span style={{ color: "purple", fontSize: "16px" }}>
                {" "}
                {user.name}
              </span>
            </MenuItem>
          )}
          <MenuItem onClick={() => history.push("/productList")}>
            PRODUCTS
          </MenuItem>
          <MenuItem onClick={() => history.push("/register")}>
            REGISTER
          </MenuItem>
          {user.name !== "" ? (
            <MenuItem onClick={logout}>LOGOUT</MenuItem>
          ) : (
            <MenuItem onClick={() => history.push("/login")}>SIGN IN</MenuItem>
          )}

          <MenuItem onClick={() => history.push("/cart")}>
            <Badge badgeContent={cart?.length} color="primary">
              <ShoppingCartOutlined />
            </Badge>
          </MenuItem>
        </Right>
      </Wrapper>
    </Container>
  );
};

export default Navbar;
